/* temp menu style from https://www.mapbox.com/mapbox-gl-js/example/toggle-layers/ */
.layersMenu {
    background: #fff;
    position: absolute;
    z-index: 10;
    top: 5px;
    right: 5px;
    border-radius: 3px;
    width: 120px;
    border: 1px solid rgba(0,0,0,0.4);
    font-family: 'Open Sans', sans-serif;
}

.layersMenu a {
    font-size: 13px;
    color: #404040;
    display: block;
    margin: 0;
    padding: 0;
    padding: 10px;
    text-decoration: none;
    border-bottom: 1px solid rgba(0,0,0,0.25);
    text-align: center;
}

.layersMenu a:last-child {
    border: none;
}

.layersMenu a:hover {
    background-color: #f8f8f8;
    color: #404040;
}

.layersMenu a.active {
    background-color: #005896;
    color: #ffffff;
}

.layersMenu a.active:hover {
    background: #0079ce;
}

.tray{
    z-index: 10;
    position: absolute;
    background-color: rgba(255, 255, 255,0.0);
    display: inline-block;
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
}

.trayHeader{
    margin-left: 85%;
}

@media (max-width: 30em) {
    .trayHeader {
        margin-left: 70%;
    }
}

@media (min-width: 30em) and (max-width: 48em){
    .trayHeader {
        margin-left: 80%;
    }
}

.button {
    background: rgba(94, 99, 122, 0.9);
    border: 1px solid rgba(60, 64, 79, 0.8);
    color: #ffffff;
    padding: 10px;
    font-size: 16px;
    border-bottom: none;
}

.button:hover {
    background: rgba(60, 64, 79, 0.8);
    color: #ffffff;
}

button:focus {
    outline:0;
}

.trayHeader div{
    background: rgba(155,155,155,0.7);
    padding: 2px;
}

.trayBody{
    background: white;
    height: 180px;
    display: -ms-flexbox;
    display: flex;
    overflow-x: auto;
    padding: 10px 3px;
}

.trayBody div{
    width: 300px;
    padding: 3px;
    margin: 3px;
    background: rgba(155,155,155,0.7)
}

.hidden{
    display: none;
}




