.signup {
    margin-top: 10%;
    margin-left: 10%;
}

.form label{
    line-height: 2rem;
}

.form input {
    padding: 10px;
    width: 94%;
    font-size: 12px;
}

.clearfix {
    margin-top: 10px;
}

.cancelbtn, .signupbtn {
    width: 50%;
    /* float: left; */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
  
.cancelbtn {
    background-color: #e7e7e7; /* Gray */ 
    color: black;
} 
.cancelbtn:hover {
    background-color: #bababa;
}
.signupbtn {
    background-color: #555555; /* Black */
} 
.signupbtn:hover {
    background-color: #333333;
}

@media(max-width: 48em){
    .modal{
        left: 15%;
    }
}

@media(max-width: 30em){
    .modal{
        left: 0.5%;
        top: 15%;
        width: 350px;
        padding: 10px;
        height: 380px;
    }
    .form label{
        line-height: 1rem;
    }
}


